		wxLogin() {
		    let _this = this;
		    uni.showLoading({
		        title: '登录中...'
			});   
		    // 1.wx获取登录用户code
		    uni.login({
		        provider: 'weixin',
				success: function(loginRes) {
		            let code = loginRes.code;
		            if (!_this.isCanUse) {
		                //非第一次授权获取用户信息
		                uni.getUserInfo({
		                    provider: 'weixin',
		                    success: function(infoRes) {
		 　　　　　　　　　　　　//获取用户信息后向调用信息更新方法
		                        let nickName = infoRes.userInfo.nickName; //昵称
		                        let avatarUrl = infoRes.userInfo.avatarUrl; //头像
		                    }
		                });
		            }
